


/* Media query for mobile devices */
@import "statics";
nav {
  width: 100%;
  height: 100px;
  background-color: $color1;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 $p;
  position: sticky;
  top: 0%;
  z-index: 60;

  >h2 {
      font-size: 2rem;
  }

  >div {
      display: flex;
      align-items: center;
      gap: 2rem;

      a {
          color: $color2;
          font-weight: 500;
          transition: all 0.3s;
          letter-spacing: 1px;

          &:hover {
              color: $color3;
          }
      }
  }

  >a>button {
      border: 1px solid $color6_1;
      padding: 0.5rem 2rem;
      background-color: $color1;
      cursor: pointer;
      color: $color6;
      letter-spacing: 1px;
      font-weight: 500;
      transition: all 0.3s;

      &:hover {
          background-color: $color6;
          color: $color4;
      }
  }
}

.navBtn {
  width: 40px;
  height: 43px;
  border: none;
  background-color: black;
  color: red;
  font-size: 1.2rem;
  z-index: 150;
  position: fixed;
  top: 1.5rem;
  right: 2rem;
  display: none;
  
  padding-right: 2rem;


  &:hover {
      color: black;
      background-color: rgb(110, 110, 149);
  }
}

.navPhone {
  transition: all 0.5s;
  transform: translateY(-200%);
  width: 100%;
  height: 100vh;
  background-color: $color5;
  padding: $p_base;
  position: fixed;
  top: 0;
  z-index: 100;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 3rem;

  >h2 {
      font-size: 2rem;
      color: $color4;
  }

  >div {
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 2rem;

      a {
          color: $color4;
          letter-spacing: 1px;
          font-weight: 500;
          transition: all 0.3s;

          &:hover {
              color: $color3;
          }
      }
  }

  >a >button {
      border: 1px solid $color6_1;
      padding: 0.5rem 2rem;
      background-color: $color1;
      cursor: pointer;
      color: $color6;
      letter-spacing: 1px;
      font-weight: 500;
      transition: 0.5s;
      &:hover {
          background-color: $color6;
          color: $color4;


  }
}}
.navPhoneComes {
  transform: translateY(0);
}




@media screen and (max-width: 1200px) {
  nav {
      padding: 2rem;
  }
}
@media screen and (max-width: 1100px) {
  nav {
      padding: 0 $p_base;
  }
}

@media screen and (max-width: 930px) {
  nav {
      padding: 0 $p_base;

      >h2 {
          font-size: 1.8rem;
      }

      >div {
          gap: 1rem;
      }
  }
}
@media screen and (max-width: 800px) {
  nav {

      >div,
      >a {
          display: none;
      }
  }

  .navBtn {
      display: block;
  }
}

.login{
  font-size: 1.3rem;
}